<template>
  <div>
    <div style="margin-top: 20px">
      <span>调度中心运行状态：</span>
      <el-switch
          v-model="schedulerState"
          active-color="#13ce66"
          inactive-color="#cccccc"
          @change="startOrstopScheduler">
      </el-switch>
      <span v-if="schedulerState==true">运行中</span>
      <span v-else>停止</span>
    </div>
    <div style="margin-top: 30px">
      <span class="demonstration">当前正在运行的任务列表：</span>
      <el-table
          :data="list"
          style="width: 100%">
        <el-table-column
            prop="name"
            label="任务名称"
            width="180">
        </el-table-column>
        <el-table-column
            prop="startTime"
            label="任务开始时间"
            width="180"
            :formatter="showTime">
        </el-table-column>
      </el-table>
    </div>
  </div>
</template>

<script>
import {listRunningJob,statusScheduler,startScheduler,stopScheduler} from "@/utils/api";
export default {
  name: "SchedulerManager",
  data() {
    return {
      schedulerState: false,
      list:[]
    }
  },
  computed: {

  },
  mounted() {
    this.getTableData()
  },
  methods: {
    getTableData() {
      listRunningJob().then((response) => {
        this.list = response.data
      })
      statusScheduler().then((response) => {
        this.schedulerState = response.data.flag
      })
    },
    startOrstopScheduler() {
      console.log(this.schedulerState)
      if(this.schedulerState) {
        console.log("开启调度器")
        startScheduler().then(() => {
          this.getTableData();
        })
      } else {
        console.log("停止调度器")
        stopScheduler().then(() => {
          this.getTableData();
        })
      }
    },
    showTime(row,column,cellValue) {
      var date = new Date(cellValue);
      return date.Format("yyyy-MM-dd HH:mm:ss")
    }

  }
}
</script>

