<template>

  <div>
    <el-tabs v-model="activeName" @tab-click="handleClick">
      <el-tab-pane label="调度中心" name="scheduler-manager"></el-tab-pane>
      <el-tab-pane label="任务管理" name="task-manager"></el-tab-pane>
      <el-tab-pane label="任务日志" name="task-logger"></el-tab-pane>
    </el-tabs>
    <router-view></router-view>
  </div>

</template>

<script>
export default {
  name: "SchedulerPage",
  data() {
    return {
      activeName: ''
    }
  },
  mounted() {
    var path = this.$router.currentRoute.path;
    var name = path.substring(path.lastIndexOf("/")+1)
    console.log(name)
    this.activeName = name
  },
  methods: {
    handleClick(tab) {
      console.log(tab)

      this.$router.push("/task/"+tab.name)
    }
  }

}
</script>

