<template>
  <div>
    <el-form :inline="true" :model="criteria" class="demo-form-inline">
      <el-form-item label="任务名称">
        <el-input v-model="criteria.name" placeholder="任务名称"></el-input>
      </el-form-item>
      <el-form-item label="执行结果">
        <el-select v-model="criteria.state" placeholder="执行结果">
          <el-option label="正常" value="正常"></el-option>
          <el-option label="异常" value="异常"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="getTasklog">查询</el-button>
      </el-form-item>
    </el-form>
    <el-table
        :data="logs"
        style="width: 100%">
      <el-table-column
          prop="name"
          label="任务名称"
          width="180"
          sortable="custom">
      </el-table-column>
      <el-table-column
          prop="startTime"
          label="开始时间"
          width="180"
          :formatter="showTime">
      </el-table-column>
      <el-table-column
          prop="endTime"
          label="结束时间"
          width="180"
          :formatter="showTime">
      </el-table-column>
      <el-table-column
          prop="duration"
          label="持续时长(ms)"
          width="180">
      </el-table-column>
      <el-table-column
          prop="state"
          label="执行结果"
          width="180">
      </el-table-column>
      <el-table-column
          prop="exceptionBrief"
          label="异常摘要"
          width="180">
      </el-table-column>
      <el-table-column
          prop="address"
          label="异常详情">
      </el-table-column>
      <el-table-column
          fixed="right"
          label="操作"
          width="100">
        <template slot-scope="scope">
          <el-popover
              placement="left"
              width="1000"
              trigger="click">
            <el-input type="textarea" autosize v-model="scope.row.exceptionDetail"></el-input>
            <el-button slot="reference">查看</el-button>
          </el-popover>
        </template>
      </el-table-column>
    </el-table>
    <div class="block">
      <el-row>
        <el-col :span="8" :offset="8">
          <el-pagination
              @size-change="handleSizeChange"
              @current-change="handleCurrentChange"
              :current-page="criteria.pageCur"
              :page-sizes="[5, 8, 10, 20]"
              :page-size="criteria.pageSize"
              layout="total, sizes, prev, pager, next, jumper"
              :total="criteria.total">
          </el-pagination>
        </el-col>
      </el-row>

    </div>
  </div>

</template>

<script>
import {listJobLogs} from '@/utils/api'
export default {
  name: "TaskLogger",
  data() {
    return {
      logs:[],
      criteria: {
        name:'',
        state: '',
        pageSize: 5,
        pageCur: 1,
        total: 0
      }
    }
  },
  mounted() {
    this.getTasklog()
  },
  methods: {
    getTasklog() {
      console.log('获取服务器数据')
      listJobLogs({
        name: this.criteria.name,
        pageCur: this.criteria.pageCur,
        pageSize: this.criteria.pageSize,
        state: this.criteria.state
      }).then((response) => {
        console.log(response)
        this.logs = response.data.data
        this.criteria.total = response.data.count;
      })
    },
    onSubmit() {

    },
    handleSizeChange(size) {
      this.criteria.pageSize = size
      this.getTasklog()
    },
    handleCurrentChange(pageCur) {
      this.criteria.pageCur = pageCur
      this.getTasklog()
    },
    showTime(row,column,cellValue) {
      var date = new Date(cellValue);
      return date.Format("yyyy-MM-dd HH:mm:ss")
    }
  }
}
</script>

