<template>
  <div>
    <el-form :inline="true" :model="criteria" class="demo-form-inline">
      <el-form-item label="任务名称">
        <el-input v-model="criteria.name" placeholder="任务名称"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="getTableData">查询</el-button>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="editJob(null)">新建任务</el-button>
      </el-form-item>
    </el-form>
    <el-table
        :data="list"
        style="width: 100%">
      <el-table-column
          prop="name"
          label="任务名称"
          width="180"
          sortable="custom">
      </el-table-column>
      <el-table-column
          prop="description"
          label="任务描述"
          width="180">
      </el-table-column>
      <el-table-column
          prop="targetObject"
          label="任务Bean"
          width="180">
      </el-table-column>
      <el-table-column
          prop="targetMethod"
          label="任务方法"
          width="180">
      </el-table-column>
      <el-table-column
          prop="triggerType"
          label="触发器类型"
          width="180"
          :formatter="showTriggerType">
      </el-table-column>
      <el-table-column
          prop="enable"
          label="任务状态"
          width="180"
          :formatter="showJobStatus">
      </el-table-column>
      <el-table-column
          label="操作"
          width="180">
        <template slot-scope="scope">
          <el-button @click="scheJob(scope.row)" type="text" size="small" v-if="!scope.row.enable">调度</el-button>
          <el-button @click="unscheJob(scope.row)" type="text" size="small" v-else>取消调度</el-button>
          <el-button @click="editJob(scope.row)" type="text" size="small">编辑</el-button>
          <el-button @click="deleteJob(scope.row)" type="text" size="small">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    <div class="block">
      <el-row>
        <el-col :span="8" :offset="8">
          <el-pagination
              @size-change="handleSizeChange"
              @current-change="handleCurrentChange"
              :current-page="criteria.pageCur"
              :page-sizes="[5, 8, 10, 20]"
              :page-size="criteria.pageSize"
              layout="total, sizes, prev, pager, next, jumper"
              :total="criteria.total">
          </el-pagination>
        </el-col>
      </el-row>

    </div>
<!--    <AddOrEditTask ref="addOrEdit" :dialogVisible="openEditDialog" :openOrCloseDialog="openOrCloseDialog"></AddOrEditTask>-->
  </div>

</template>

<script>

import { listJobs,delJob,scheJob,unscheJob } from "@/utils/api"
export default {
  name: "TaskManager",
  data() {
    return {
      list:[],
      criteria: {
        name:'',
        state: '',
        pageSize: 5,
        pageCur: 1,
        total: 0
      },
      openEditDialog: false

    }
  },
  mounted() {
    this.getTableData()
  },
  methods: {
    getTableData() {
      console.log('获取服务器数据')
      listJobs({
        name: this.criteria.name,
        pageCur: this.criteria.pageCur,
        pageSize: this.criteria.pageSize
      }).then((response) => {
        console.log(response)
        this.list = response.data.data;
        this.criteria.total = response.data.count;
        //this.criteria.total = parseInt(response.headers['x-total-count'])
      })
    },
    onSubmit() {

    },
    handleSizeChange(size) {
      this.criteria.pageSize = size
      this.getTableData()
    },
    handleCurrentChange(pageCur) {
      this.criteria.pageCur = pageCur
      this.getTableData()
    },
    editJob(data) {
      console.log(data)
      if(data) {
        this.$router.push("/task/task-edit/"+data.id)
      } else {
        console.log("新建页面")
        this.$router.push("/task/task-edit/"+0)
      }


      /*this.openEditDialog = true
      this.$nextTick(() => {
        this.$refs.addOrEdit.init(data)
      })*/
    },
    deleteJob(data) {
      delJob({id:data.id}).then((response) => {
        console.log(response)
        this.getTableData()
      })
    },
    scheJob(data) {
      scheJob({id:data.id}).then((response) => {
        console.log(response)
        this.getTableData()
      })
    },
    unscheJob(data) {
      unscheJob({id:data.id}).then((response) => {
        console.log(response)
        this.getTableData()
      })
    },
    openOrCloseDialog(flag) {
      this.openEditDialog = flag
      this.getTableData()
    },
    showTriggerType(row,column,cellValue) {
      if(cellValue=='RIGHTNOW') {
        return "立即执行"
      } else if(cellValue=='DELAY') {
        return "指定时间"
      } else {
        return "循环执行"
      }
    },
    showJobStatus(row,column,cellValue) {
      if(cellValue) {
        return "已调度"
      } else {
        return "未调度"
      }
    }
  }
}
</script>

