<template>
  <el-row :gutter="20">
    <el-col :span="6" style="height: 20px"></el-col>
    <el-col :span="12">
      <el-form label-width="120px" :model="data" :rules="rules" ref="jobForm">
        <el-form-item label="任务名称" prop="name">
          <el-input v-model="data.name"></el-input>
        </el-form-item>
        <el-form-item label="任务对象" prop="targetObject" >
          <el-select v-model="data.targetObject" @change="handleTargetObjectChanage">
            <el-option v-for="(targetObject,index) in task" :key="index" :label="targetObject.alias" :value="targetObject.name"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="任务方法" prop="targetMethod" >
          <el-select v-model="data.targetMethod" @change="handleTaskSelected">
            <el-option v-for="(method,index) in taskMethodMetas" :key="index" :label="method.alias" :value="method.name" ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item v-for="(param,index) in taskParamMetas" :key="index" :label="param.name" >
          <el-input type="number" name="param" v-model="data.params[index]" v-if="param.type == 'int'"></el-input>
          <el-date-picker type="date" placeholder="选择日期" v-model="data.params[index]" style="width: 100%;" v-else-if="param.type == 'java.util.Date'"></el-date-picker>
          <el-input type="text" name="param" v-model="data.params[index]" v-else ></el-input>
        </el-form-item>
        <el-form-item label="任务描述">
          <el-input type="textarea" autosize v-model="data.description"></el-input>
        </el-form-item>
        <el-form-item label="触发器类型" prop="triggerType">
          <el-select v-model="data.triggerType">
            <el-option label="立即执行" value="RIGHTNOW"></el-option>
            <el-option label="指定时间" value="DELAY"></el-option>
            <el-option label="循环执行" value="REGULAR"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="执行时间" prop="fireTime" v-if="data.triggerType == 'DELAY'">
          <el-date-picker type="datetime" placeholder="选择日期" v-model="data.fireTime" style="width: 100%;" ></el-date-picker>
        </el-form-item>
        <el-form-item label="cron表达式" prop="cronExpression" v-if="data.triggerType == 'REGULAR'">
          <el-input v-model="data.cronExpression"></el-input>
          <el-button type="primary" @click="showDialog">生成 cron</el-button>
        </el-form-item>
        <el-dialog title="生成 cron" :visible.sync="showCron">
          <vcrontab @hide="showCron=false" @fill="crontabFill" :expression="data.cronExpression"></vcrontab>
        </el-dialog>
        <el-form-item label="异常处理策励" prop="strategy">
          <el-select v-model="data.strategy">
            <el-option label="继续" value="GOON"></el-option>
            <el-option label="中止" value="BREAK"></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div style="">
        <el-button @click="exitEdit">取 消</el-button>
        <el-button type="primary" @click="saveTask">确 定</el-button>
      </div>
    </el-col>
    <el-col :span="6"><div class="grid-content bg-purple"></div></el-col>
  </el-row>
</template>

<script>

import vcrontab from "vcrontab";
import {getJobsMetadata,saveJobs,getJob} from "@/utils/api";

export default {
  name: "AddOrEditTask",
  data() {
    return {
      data: {
        params: []
      },
      task: [],
      input: "",
      expression: "",
      showCron: false,
      rules: {
        name: [
          { required: true, message: '请输入任务名称', trigger: 'blur' }
        ],
        targetObject: [
          { required: true, message: '请选择任务对象', trigger: 'blur' }
        ],
        targetMethod: [
          { required: true, message: '请选择任务方法', trigger: 'blur' }
        ],
        triggerType: [
          { required: true, message: '请选择执行类型', trigger: 'blur' }
        ],
        strategy: [
          { required: true, message: '请选择异常处理策略', trigger: 'blur' }
        ],
        fireTime: [
          { required: true, message: '请选择执行时间', trigger: 'blur' }
        ],
        cronExpression: [
          { required: true, message: '请选择执行时间', trigger: 'blur' }
        ],
      }

    }
  },
  mounted() {
    this.getTaskMetaData()
    this.retriveJob()
  },
  computed: {
    taskMethodMetas: function () {
      var taskObject = this.task.find(item => {
        if(this.data.targetObject&&item.name==this.data.targetObject) {
          return true
        }
        return false
      })
      if(taskObject!=null){
        return taskObject.methods
      }
      return [];
    },
    taskParamMetas: function () {

      var taskMethod = this.taskMethodMetas.find(item => {
        if(this.data.targetMethod&&item.name==this.data.targetMethod) {
          return true
        }
        return false
      })
      if(taskMethod!=null) {
        return taskMethod.params
      }
      return []
    }
  },
  methods: {
    getTaskMetaData(){
      getJobsMetadata({}).then(response => {
        this.task = response.data
        console.log(response)
      })
    },
    retriveJob() {
      const id = this.$route.params.id
      if(id>0) {
        getJob({id}).then((response) => {
          console.log(response)
          var data = response.data
          if(data.targetParams) {
            data.params= data.targetParams.split(",")
          } else {
            data.params = []
          }
          this.data = response.data
        })
      }
    },
    handleTargetObjectChanage() {
      this.data.targetMethod = null;
      this.data.description = "";
    },
    init(data) {
      if(data) {
        if(data.targetParams) {
          data.params= data.targetParams.split(",")
        }

        this.data = data
      }
    },
    handleClose() {
      this.data = {
        params: []
      }
      this.openOrCloseDialog(false)
    },
    handleTaskSelected(value) {
      console.log("选中"+value);
      var taskMethod = this.taskMethodMetas.find(item => {
        if(this.data.targetMethod&&item.name==this.data.targetMethod) {
          return true
        }
        return false
      })
      this.data.description = taskMethod.description;
    },
    saveTask() {
      this.$refs["jobForm"].validate((valid) => {
        if (valid) {
          this.dosaveTask()
        } else {
          return false;
        }
      });

    },
    dosaveTask() {
      var data = new Object()
      Object.assign(data,this.data)
      if(this.data.params) {
        data.targetParams = this.data.params.join(",")
      }
      saveJobs(data).then(response => {
        //this.task = response.data
        console.log(response)
        this.data = {
          params: []
        }
        this.$router.back()
        //this.openOrCloseDialog(false)
      })
    },
    exitEdit() {
      this.$router.back()
    },
    crontabFill(value) {
      //确定后回传的值
      this.data.cronExpression = value;
    },
    showDialog() {
      this.showCron = true;
    }
  },
  components: {vcrontab},
  props:['dialogVisible','openOrCloseDialog']
}
</script>

