import SchedulerPage from "@/pages/SchedulerPage.vue";
import SchedulerManager from "@/pages/SchedulerManager.vue";
import TaskManager from "@/pages/TaskManager.vue";
import TaskLogger from "@/pages/TaskLogger.vue";
import AddOrEditTask from "@/pages/AddOrEditTask.vue";

const routes = [

    {
        path: '/task',
        component: SchedulerPage,
        children: [
            {
                path: 'scheduler-manager',
                component: SchedulerManager
            },
            {
                path: 'task-manager',
                component: TaskManager
            },
            {
                path: 'task-logger',
                component: TaskLogger
            },
            {
                path: 'task-edit/:id',
                component: AddOrEditTask
            },
        ]
    },
    {
        path: '/**',
        redirect: '/task/task-manager'
    }
]

export default routes