// 获取任务元数据方法
// http://localhost:8821/order-service/order/easyjob/
const baseurl = "";
export function getJobsMetadata(params={}) {
    return http({
        url: baseurl + 'jobmetadata.json',
        method: 'post',
        data: params
    })
}

export function listJobs(params={}) {
    return http({
        url: baseurl + 'listjob.json',
        method: 'post',
        data: params
    })
}

export function saveJobs(params={}) {
    console.log("post:"+params)
    const jsonString1 = JSON.stringify(params)
    console.log("post:"+jsonString1)
    return http({
        url: baseurl + 'savejob.json',
        method: 'post',
        data: jsonString1
    })
}

